.load {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

button {
  background: black;
  color: white;
  font-family: "Times New Roman";
  border: none;
  padding: 5px;
  cursor: pointer;
}

.title {
  font-size: 100px;
  background: black;
  color: white;
  font-family: "Times New Roman";
  margin-bottom: 10px;
  padding: 10px;
  width: 100%;
}

.inputBack {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  .inputContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;

    textarea {
      margin-bottom: 10px;
      height: 50%;
      width: 100%;
      resize: none;

      &:focus {
        border: none;
        outline: 1px solid black;
      }
    }
  }
}

.demotivatorContainer {
  display: flex;
  justify-content: center;

  .demotivator {
    background: black;
    padding: 100px 100px 30px 100px;
    margin-top: 5vh;

    .imgContainer {
      border: 2px solid white;
      padding: 7px;
      height: 50vh;
      display: flex;
      justify-content: center;

      img {
        height: 100%;
      }
    }

    .textContainer {
      font-size: 100px;
      color: white;
      display: flex;
      justify-content: center;
      font-family: "Times New Roman";
    }

    .secondTextContainer {
      font-size: 50px;
      color: white;
      display: flex;
      justify-content: center;
      font-family: "Times New Roman";
    }
  }
}

.buttonContainer {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}